// Extra

.resetter{
  position: relative;
}

/* 
1024
 */
.lsv-nav{
  background-color: @lsv_menu_color;
  height: @lsv_menu_height;
}

.lsv-nav--valign{
  position: absolute;
  top: 0;
  bottom: 0;
  left: @lsv_logo_displace;
  width: 0;
  height: 67%;
  margin: auto;
}

.lsv-nav__logo{
  position: absolute;
  top: 0;
  bottom: 0;
  left: @lsv_logo_displace;
  width: 0;
  height: 67%;
  margin: auto;
}

.lsv-nav__menu-btn{
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: @lsv_menu_height;
  border: 0;
  padding: 0;
  background-color: @lsv_green;

  &:hover{
    background-color: @lsv_green_active;
  }

  i{
    color: white;
    font-size: 24px;
  }
}

.bars-container {
    position: relative;
}

.line {
    display: block;
    position: relative;
    top: 2px;
    height: 3px;
    width: 25px;
    margin: 4px auto;
    background-color: #fff;
    transition: all 300ms cubic-bezier(.215,.61,.355,1);
    transform: translate3d(0,0,0);
}

.line01{
  transition-delay: 210ms;
}
.line.active{
  top: -4px;
}
.line01.active{
  opacity: 0;
  // transition-delay: 210ms;
}

.line02{
  transition-delay: 140ms;
  
}
.line02.active{
  // transition-delay: 140ms;
}
.line03{
  transition-delay: 70ms;
}
.line03.active{
  // transition-delay: 70ms;
}
.line04{
  opacity: 0;
  transition-delay: 0ms;
}
.line04.active{
  // transition-delay: 0ms;
  opacity: 1;
}

.lsv-nav__links{
  float:right;
  margin: 11px;
}

.lsv-btn{
  color: white;
  border-radius: 40px;
  background-color: @lsv_grey;
  &:hover{
    background-color: @lsv_green;
  }
  span,i{
    color: white;
    font-size: 14px;
  }
  transition: all 500ms cubic-bezier(.215,.61,.355,1);
}

.lsv-menu{
//  position: absolute;
  width: 100%;
  top: @lsv_menu_height;
  background-color: @lsv_green;
  color:white;
  
}

.lsv-menu__list, .lsv-menu__sublist{
  display: block;
  list-style-type: none;
  -webkit-padding-start: 0;
  padding: 25px 0px;
}
.lsv-menu__sublist-item{
}

.lsv-menu__list{
}

.lsv-menu__list-item{
  display: inline-block;

  span{
    text-decoration: underline;
  }
}

.lsv-menu__link{
  text-decoration: underline;
}

.lsv-menu__link , .lsv-menu__sublink{
  
  color: white;
}